.table-responsive {
  overflow-x: auto;
  overflow-y: hidden;
}

table.dataTable {
  margin: 0 !important;
}

table tr td{
    border-bottom: 1px solid #e0e6ed !important;
}

.table-hover > tbody > tr:hover td {
    background-color: #f4f6ff !important;
    color: #515365;
}

.table > {
  thead {
    border-top: none;
    border-bottom: none;

    > tr > th {
      text-transform: initial;
      font-weight: 600;
      border-top: none;
      border-right: none;
      border-left: none;
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 0;
      -webkit-transition: all 0.1s ease;
      transition: all 0.1s ease;
      padding: 10px 21px 10px 21px;
      font-weight: 600;
      font-size: 14px;
      white-space: nowrap;

      &:first-child {
        &:before, &:after {
          display: none;
        }
      }

      &:last-child {
        &:before, &:after {
          display: none;
        }
      }
    }
  }

  tbody {
    &:before {
      display: none;
    }

    > tr {
      > td {
        &:first-child {
          border-top-left-radius: 8px;
        }

        padding: 0;
        padding: 10px 21px 10px 21px;
        white-space: nowrap;

        .inv-number {
          color: $primary;
          cursor: pointer;
          font-size: 16px;
          text-align: left;
        }

        .user-name {
          color: $dark;
          font-size: 14px;
          margin-bottom: 0;
          overflow: hidden;
          text-align: left;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-wrap: break-word;
        }

        .inv-email {
          color: #506690;
          font-size: 14px;
          margin-bottom: 0;
          margin-top: 0;
          overflow: hidden;
          text-align: left;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-wrap: break-word;

          svg {
            width: 17px;
            height: 17px;
            vertical-align: text-bottom;
            color: $secondary;
            stroke-width: 1.5;
          }
        }

        .inv-date svg {
          width: 17px;
          height: 17px;
          vertical-align: text-top;
          color: $info;
          stroke-width: 1.5;
        }

        .dropdown {
          .dropdown-toggle svg {
            stroke-width: 1px;
          }

          &.show .dropdown-toggle svg {
            stroke-width: 1px;
            color: #7367f0;
          }

          &:not(.custom-dropdown-icon):not(.custom-dropdown) .dropdown-menu {
            padding: 9px !important;
          }
        }
      }

      &:last-child > td .dropdown:not(.custom-dropdown-icon):not(.custom-dropdown) .dropdown-menu, &:nth-last-child(2) > td .dropdown:not(.custom-dropdown-icon):not(.custom-dropdown) .dropdown-menu {
        top: -94px !important;
      }

      &:last-child > td .dropdown:not(.custom-dropdown-icon):not(.custom-dropdown) .dropdown-menu.show, &:nth-last-child(2) > td .dropdown:not(.custom-dropdown-icon):not(.custom-dropdown) .dropdown-menu.show {
        top: -90px !important;
      }

      > td .dropdown:not(.custom-dropdown-icon):not(.custom-dropdown) .dropdown-menu a.dropdown-item svg {
        width: 16px;
        height: 16px;
        margin-right: 7px;
        vertical-align: text-top;
      }
    }
  }
}

/*
    Inv List Bottom Section
*/

.inv-list-bottom-section {
  padding: 15px;
}

.user-select-assigned{
  border-radius: 5px;
    color: #515365;
    border-color: #dadada;
    box-shadow: 0px 0px 10px 0px #0000001f;
    padding: 2px 1px;
    cursor: pointer;
}


.page-item .page-link:hover {
    background: #191e3a;
    color: #bfc9d4;
  }

  .page-link {
    margin-right: 5px;
    border-radius: 8px;
    background: rgba(0, 23, 55, 0.08);
    border: none;
    color: #888ea8;
    height: 33px;
    width: 33px;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    &:focus {
      box-shadow: none;
    }
  }

  .page-item {
    &.disabled .page-link {
      background: transparent;

      svg {
        color: #888ea8;
      }
    }

    &:first-child .page-link {
      border-radius: 8px;
      padding: 0;
      height: 33px;
      width: 33px;
      display: inline-flex;
      justify-content: center;
      align-items: center;

      svg {
        width: 17px;
      }
    }

    &:last-child .page-link {
      border-radius: 8px;
      padding: 0;
      height: 33px;
      width: 33px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
    }

    &.next:not(.disabled) .page-link, &.previous:not(.disabled) .page-link {
      background: #1b2e4b;
    }

    &.next:not(.disabled) .page-link svg, &.previous:not(.disabled) .page-link svg {
      color: #fff;
    }

    &:last-child .page-link svg {
      width: 17px;
    }

    &.active .page-link {
      background-color: $primary;
    }
  }

  .infotable{
    padding-top: 0.85em;
    white-space: normal;
    color: $primary;
    font-weight: 600;
    border: 1px solid #e0e6ed;
    display: inline-block;
    padding: 10px 16px;
    border-radius: 6px;
    font-size: 13px;
  }

  .listpagination .pagination{
    float: right;
    margin-bottom: 0px;
  }

